import React, { useState, useEffect } from 'react';
import emidsLogo from '../emids-logo.png'; // Adjust path as needed

function ProjectSetupPage({ projectDetails, onSave }) {
  const [formData, setFormData] = useState({
    projectName: projectDetails.projectName || '',
    supervisorPrompt: projectDetails.supervisorPrompt || '',
    finalResponsePrompt: projectDetails.finalResponsePrompt || '',
    modelProvider: projectDetails.modelProvider || 'azure-openai',
    apiKey: projectDetails.apiKey || '',
    apiUrl: projectDetails.apiUrl || '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onSave(formData);
  };

  // Define model providers
  const modelProviders = [
    { value: 'azure-openai', label: 'Azure OpenAI' },
    { value: 'openai', label: 'OpenAI' },
    { value: 'google-gemini', label: 'Google Gemini' },
    { value: 'anthropic', label: 'Anthropic Claude' }
  ];

  return (
    <div className="project-setup-container">
      <div className="emids-logo">
        <img src={emidsLogo} alt="EMIDS Logo" />
      </div>
      
      <h1 className="page-title">Project Setup</h1>
      <p className="page-subtitle">Configure your Multi-Agent AI project</p>
      
      <form className="setup-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="projectName">Project Name</label>
          <input
            type="text"
            id="projectName"
            name="projectName"
            className="form-control"
            value={formData.projectName}
            onChange={handleChange}
            required
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="supervisorPrompt">Orchestrator</label>
          <textarea
            id="supervisorPrompt"
            name="supervisorPrompt"
            className="form-control"
            rows="4"
            value={formData.supervisorPrompt}
            onChange={handleChange}
            required
          ></textarea>
        </div>
        
        <div className="form-group">
          <label htmlFor="finalResponsePrompt">Final Response Prompt</label>
          <textarea
            id="finalResponsePrompt"
            name="finalResponsePrompt"
            className="form-control"
            rows="4"
            value={formData.finalResponsePrompt}
            onChange={handleChange}
            required
          ></textarea>
        </div>
        
        <div className="form-group">
          <label htmlFor="modelProvider">Select Model</label>
          <div className="select-wrapper">
            <select
              id="modelProvider"
              name="modelProvider"
              className="form-control model-select"
              value={formData.modelProvider}
              onChange={handleChange}
              required
            >
              {modelProviders.map(provider => (
                <option key={provider.value} value={provider.value}>
                  {provider.label}
                </option>
              ))}
            </select>
          </div>
        </div>
        
        <div className="api-config-section">
          <h3>API Configuration</h3>
          <div className="form-group">
            <label htmlFor="apiKey">API Key</label>
            <input
              type="password"
              id="apiKey"
              name="apiKey"
              className="form-control"
              value={formData.apiKey}
              onChange={handleChange}
              required
              placeholder={`Enter your ${formData.modelProvider} API key`}
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="apiUrl">API Endpoint URL</label>
            <input
              type="text"
              id="apiUrl"
              name="apiUrl"
              className="form-control"
              value={formData.apiUrl}
              onChange={handleChange}
              placeholder={`Enter the ${formData.modelProvider} endpoint URL (if required)`}
            />
          </div>
        </div>
        
        <div className="form-actions">
          <button type="submit" className="btn-save">Save</button>
        </div>
      </form>
    </div>
  );
}

export default ProjectSetupPage;