// ProjectDashboard.jsx
import React, { useState, useEffect } from 'react';
import emidsLogo from '../emids-logo.png';
import ApiService from '../services/apiservices';
import './styles.css';

function ProjectDashboard({ onNavigate, onCreateNewProject }) {
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchProjects();
  }, []);

  const fetchProjects = async () => {
    try {
      setLoading(true);
      const response = await ApiService.getProjects();
      setProjects(response);
    } catch (error) {
      console.error('Failed to fetch projects:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleStartStop = async (projectId, currentStatus) => {
    try {
      const endpoint = currentStatus === 'Running' ? 'stopProject' : 'startProject';
      const response = await ApiService[endpoint](projectId);
      
      if (response.status) {
        // Update local state
        setProjects(prev => prev.map(project => 
          project.id === projectId 
            ? { ...project, status: response.status }
            : project
        ));
      }
    } catch (error) {
      console.error(`Failed to ${currentStatus === 'Running' ? 'stop' : 'start'} project:`, error);
    }
  };

  const handleAddWorkers = (projectId) => {
    onNavigate('multiAgentUI', projectId);
  };

  return (
    <div className="dashboard-container">
      <div className="dashboard-header">
        <div className="logo-title-container">
          <img src={emidsLogo} alt="EMIDS Logo" className="emids-logo" />
          <span className="admin-panel-title">PROJECT DASHBOARD</span>
        </div>
        <button 
          className="new-project-btn"
          onClick={onCreateNewProject}
        >
          New Project
        </button>
      </div>

      <div className="dashboard-content">
        {loading ? (
          <div className="loading-container">
            <div className="loading-spinner"></div>
            <p>Loading projects...</p>
          </div>
        ) : (
          <div className="projects-table-container">
            <table className="projects-table">
              <thead>
                <tr>
                  <th>S.No</th>
                  <th>Project Name</th>
                  <th>Running Status</th>
                  <th>Action</th>
                  <th>Configuration</th>
                </tr>
              </thead>
              <tbody>
                {projects.length === 0 ? (
                  <tr>
                    <td colSpan="5" className="no-projects">No projects found</td>
                  </tr>
                ) : (
                  projects.map((project, index) => (
                    <tr key={project.id}>
                      <td>{index + 1}</td>
                      <td>{project.name}</td>
                      <td>
                        <span className={`status-indicator ${project.status?.toLowerCase()}`}>
                          {project.status || 'Stopped'}
                        </span>
                      </td>
                      <td>
                        <button
                          className={`action-btn ${project.status === 'Running' ? 'stop' : 'start'}`}
                          onClick={() => handleStartStop(project.id, project.status)}
                        >
                          {project.status === 'Running' ? 'Stop' : 'Start'}
                        </button>
                      </td>
                      <td>
                        <button
                          className="action-btn add-workers"
                          onClick={() => handleAddWorkers(project.id)}
                        >
                          Add Workers
                        </button>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
}

export default ProjectDashboard;