import React, { useState, useEffect, useRef } from 'react';
import './styles.css';
import emidsLogo from '../emids-logo.png';
import ApiService from '../services/apiservices';

function ChatbotInterface({ projectId }) {
    const [messages, setMessages] = useState([]);
    const [inputMessage, setInputMessage] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [isConnected, setIsConnected] = useState(false);
    const [projectInfo, setProjectInfo] = useState(null);
    const messagesEndRef = useRef(null);

    // Scroll to bottom when new messages arrive
    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    };

    // Load project information and check health when component mounts or projectId changes
    const loadProjectInfo = React.useCallback(async () => {
        if (!projectId) return;

        try {
            const projectDetails = await ApiService.getProject(projectId);
            setProjectInfo({
                id: projectId,
                name: projectDetails.name || `Project ${projectId}`,
                status: projectDetails.status || 'unknown',
                port: projectDetails.port_number || 3000
            });

            // Check if the project is healthy (running)
            try {
                const healthCheck = await ApiService.checkProjectHealth(projectId);
                console.log('Project health check:', healthCheck);
                setIsConnected(true);
                
                // Add welcome message if it's the first load
                setMessages(prev => {
                    if (prev.length === 0) {
                        return [{
                            id: Date.now(),
                            type: 'system',
                            content: `Welcome to the AI Agent Chat Interface for Project ${projectId}. Connected to port ${projectDetails.port_number || 3000}.`,
                            timestamp: new Date().toLocaleTimeString()
                        }];
                    }
                    return prev;
                });
            } catch (healthError) {
                console.warn('Project health check failed:', healthError);
                setIsConnected(false);
                
                // Add warning message about connection
                setMessages(prev => {
                    if (prev.length === 0) {
                        return [{
                            id: Date.now(),
                            type: 'system',
                            content: `Project ${projectId} loaded but not responding. Please ensure the project is deployed and running on port ${projectDetails.port_number || 3000}.`,
                            timestamp: new Date().toLocaleTimeString()
                        }];
                    }
                    return prev;
                });
            }
        } catch (error) {
            console.error('Error loading project info:', error);
            setProjectInfo({
                id: projectId,
                name: `Project ${projectId}`,
                status: 'error',
                port: 'unknown'
            });
            setIsConnected(false);
        }
    }, [projectId]);

    // Initialize component
    useEffect(() => {
        loadProjectInfo();
    }, [loadProjectInfo]);

    const sendMessage = async () => {
        if (!inputMessage.trim() || isLoading) return;

        const userMessage = {
            id: Date.now(),
            type: 'user',
            content: inputMessage.trim(),
            timestamp: new Date().toLocaleTimeString()
        };

        setMessages(prev => [...prev, userMessage]);
        setInputMessage('');
        setIsLoading(true);

        try {
            // Use the new API method that connects to the project's port
            const responseData = await ApiService.sendChatMessage(
                projectId, 
                userMessage.content, 
                `session_${Date.now()}`
            );

            // Process the response from the supervisor agent
            const agentMessage = {
                id: Date.now() + 1,
                type: 'agent',
                content: responseData.response || responseData.message || 'No response from agent',
                agent_name: responseData.agent_name || 'Supervisor Agent',
                timestamp: new Date().toLocaleTimeString(),
                metadata: {
                    processing_time: responseData.processing_time,
                    subagents_used: responseData.subagents_used || [],
                    confidence: responseData.confidence,
                    session_id: responseData.session_id
                }
            };

            setMessages(prev => [...prev, agentMessage]);
            
            // Update connection status if successful
            if (!isConnected) {
                setIsConnected(true);
            }
        } catch (error) {
            console.error('Error communicating with supervisor agent:', error);

            let errorMessage = 'Failed to connect to supervisor agent. ';

            if (error.message.includes('Health check failed')) {
                errorMessage = `Project is not running or not responding on port ${projectInfo?.port || 'unknown'}. Please ensure the project is deployed and started.`;
            } else if (error.message.includes('Chat request failed')) {
                errorMessage = `Chat request failed. The supervisor agent on port ${projectInfo?.port || 'unknown'} is not responding. Please check:\n Project is deployed and running\n Supervisor agent is properly configured\n Port ${projectInfo?.port || 'unknown'} is accessible`;
            } else if (error.message.includes('Failed to fetch')) {
                errorMessage = `Cannot connect to project on port ${projectInfo?.port || 'unknown'}. Please check:\n Project is deployed and running\n No firewall blocking port ${projectInfo?.port || 'unknown'}\n Backend deployment service is working`;
            } else {
                errorMessage += error.message;
            }

            const errorMessageObj = {
                id: Date.now() + 1,
                type: 'error',
                content: errorMessage,
                timestamp: new Date().toLocaleTimeString()
            };

            setMessages(prev => [...prev, errorMessageObj]);
            
            // Update connection status if failed
            setIsConnected(false);
        } finally {
            setIsLoading(false);
        }
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter' && !e.shiftKey) {
            e.preventDefault();
            sendMessage();
        }
    };

    const clearChat = () => {
        setMessages([{
            id: Date.now(),
            type: 'system',
            content: `Chat cleared. Welcome back to Project ${projectId}`,
            timestamp: new Date().toLocaleTimeString()
        }]);
    };

    return (
        <div className="chatbot-container">
            {/* Header */}
            <div className="chatbot-header">
                <div className="header-left">
                    <img src={emidsLogo} alt="EMIDS Logo" className="chatbot-logo" />
                    <div className="project-info">
                        <h2>AI Agent Chat Interface</h2>
                        <p>Project ID: {projectId}</p>
                        {projectInfo?.port && <p>Port: {projectInfo.port}</p>}
                        <span className={`status-badge ${isConnected ? 'connected' : 'disconnected'}`}>
                            {isConnected ? ' Connected' : ' Disconnected'}
                        </span>
                    </div>
                </div>
                <div className="header-actions">
                    <button className="clear-chat-btn" onClick={clearChat}>
                        Clear Chat
                    </button>
                </div>
            </div>

            {/* Messages Area */}
            <div className="messages-container">
                <div className="messages-list">
                    {messages.map((message) => (
                        <div key={message.id} className={`message ${message.type}`}>
                            <div className="message-header">
                                <span className="message-sender">
                                    {message.type === 'user' ? ' You' :
                                        message.type === 'agent' ? ` ${message.agent_name || 'Agent'}` :
                                            message.type === 'system' ? ' System' : ' Error'}
                                </span>
                                <span className="message-time">{message.timestamp}</span>
                            </div>
                            <div className="message-content">
                                {message.content}
                                {message.metadata && (
                                    <div className="message-metadata">
                                        {message.metadata.processing_time && (
                                            <span className="metadata-item"> {message.metadata.processing_time}ms</span>
                                        )}
                                        {message.metadata.subagents_used && (
                                            <span className="metadata-item"> {message.metadata.subagents_used.join(', ')}</span>
                                        )}
                                        {message.metadata.confidence && (
                                            <span className="metadata-item"> {Math.round(message.metadata.confidence * 100)}%</span>
                                        )}
                                    </div>
                                )}
                            </div>
                        </div>
                    ))}

                    {isLoading && (
                        <div className="message agent loading">
                            <div className="message-header">
                                <span className="message-sender"> Agent</span>
                                <span className="message-time">Thinking...</span>
                            </div>
                            <div className="message-content">
                                <div className="typing-indicator">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                            </div>
                        </div>
                    )}

                    <div ref={messagesEndRef} />
                </div>
            </div>

            {/* Input Area */}
            <div className="input-container">
                <div className="input-wrapper">
                    <textarea
                        value={inputMessage}
                        onChange={(e) => setInputMessage(e.target.value)}
                        onKeyPress={handleKeyPress}
                        placeholder="Type your message here... (Press Enter to send, Shift+Enter for new line)"
                        className="message-input"
                        rows="2"
                        disabled={isLoading || !isConnected}
                    />
                    <button
                        onClick={sendMessage}
                        disabled={!inputMessage.trim() || isLoading || !isConnected}
                        className="send-button"
                    >
                        {isLoading ? '' : ''}
                    </button>
                </div>
                <div className="input-hint">
                    Connected to agents in project {projectId}. Ask questions and get responses from your deployed AI agents.
                </div>
            </div>
        </div>
    );
}

export default ChatbotInterface;
